    get_frequency(buf, fs) {
        let MAX_SIZE = null
        // var khz = 5000;
        let threshold = 0.013
        let autocorTh = 0.95
        // var isRecord = false;
        let max_freq = 2000;
        let bufsize = buf.length;
        let MAX_S = Math.floor(bufsize / 2);
        let best_offset = -1;
        let best_cor = 0;
        let rms = 0;
        let isGoodCor = false;
        let cors = new Array(MAX_S);
        // console.log(buf)
        // calc the whole energy of the singal
        for (let i = 0; i < bufsize; ++i) {
            rms += buf[i] * buf[i];
        }
        rms = Math.sqrt(rms / bufsize);
        // console.log(rms)
        // too low to be regarded as noise
        if (rms < threshold) return [-1,0]

        let corTemp = 1
        for (let j = 0; j < MAX_S; ++j) {
            let cor = 0
            for (let i = 0; i < MAX_S; ++i) {
                cor += Math.abs(buf[i] - buf[i + j])
            }
            cor = 1 - cor / MAX_S
            cors[j] = cor
            if (cor > autocorTh && cor > corTemp) {
                isGoodCor = true
                if (cor > best_cor) {
                    best_cor = cor
                    best_offset = j
                }
            } else if (isGoodCor) {
                let shift = (cors[best_offset + 1] - cors[best_offset - 1]) / cors[best_offset]
                let freq = fs / (best_offset + (MAX_SIZE - 1) * shift)
                if (freq < 0) freq = 0;
                if (freq > max_freq) freq = max_freq;
                return [freq, rms]
            }
            corTemp = cor;
        }
        // if (best_cor > 0.01) {
            // return fs / best_offset;
        // }
        return [-1, 0];
    }


    get_pitch(freq) {
        let pitch_index = ["C", "C#", "D", "D#", "E", "F", "F#", "G", "G#", "A", "A#", "B"]
        if (freq == -1) return ["N","0","0"]
        let pitch = 12 * (Math.log(freq / 440) / Math.log(2));
        pitch = Math.round(pitch) + 69;
        let stand_freq = 440 * Math.pow(2, (pitch - 69) / 12);
        let cents = Math.floor(1200 * Math.log(freq / stand_freq) / Math.log(2));
        let degree = Math.floor(pitch / 12) - 1;
        let note = pitch % 12;
        return [pitch_index[note], degree.toString(), cents.toString()]// + " cents &#9837";
    }


